import React, { Component } from 'react'
import ReactPlayer from 'react-player'

class Video extends Component {
    render () {
        return (
        <div >
            <ReactPlayer
            className='react-player'
            url= 'videos/geah_video_vitC.mp4'
            width='100%'
            height='100%'
            playing={false}
            controls = {true}

            />
        </div>
        )
    }_
}

export default Video;