import React, { Component } from 'react'
import ReactPlayer from 'react-player'

class Video2 extends Component {
    render () {
        return (
        <div
        >
            <ReactPlayer
            className='react-player  embed-responsive '
            url= 'videos/geah_video_reg2.mp4'
            width='100%'
            height='500px'
            playing={false}
            controls = {true}

            />
        </div>
        )
    }
}

export default Video2;