import { useParams, NavLink } from "react-router-dom";
import React from "react";

const Product = ({ data }) => {
  const { productId } = useParams();
  const product = data.find((p) => p.id === Number(productId));
  let productData;

  if (product) {
    productData = (
        <div className=" bg-white text-justify  "  >

          <div className="row ">

<div className=" col text-left ">
  <center>
  <img src={product.imageUrl} height={500} alt="bottled products" />
  </center>

          <h1><strong>
          {product.name}
            </strong></h1>
        <p><strong>DERSCRIPCION:</strong> {product.description}</p>
        <p><strong>MODO DE USO:</strong> {product.usage}</p>

        <p><strong>INGREDIENTES:</strong> {product.ingredients}</p>

        <h5><strong>STATUS:</strong> {product.status}</h5>


        <p className="text-justify">
         <strong> {product.name} </strong>
 {product.content}

        </p>
<strong>
<NavLink  to="/regeneratur">Para mas informacion, haz clic aqui</NavLink>
  </strong>

          </div>

  </div>

        </div>

    );
  } else {
    productData = <h2>Perdon. Ese producto no existe. </h2>;
  }
  return (

 <div className="bg-white text-center">
        <div  style={{ display: "flex" }}>
      <div
        style={{
          width: "100%",
          height: "40%",
          margin: "auto",
          background: "#ffffff",
          padding: "10px"
        }}
      >
        {productData}
      </div>
    </div>
    </div>
  );
};

export default Product;
